.container{ 
  background-image: url(./images/bg-intro-desktop.png);
  height:100vh;
  background-color: salmon;
  display: grid;
  grid-template-columns: repeat ( 2 auto);
  grid-template-areas:
  " main sidebar"
  "main  fills" ;
  height: 100vh;

}


.main{grid-auto-flow: main;
padding-left: 20%;
padding-top: 30%;}

